#works {
  width: 100%;
  margin-top: calc(100vh * 8 + 70vh);
  overflow: hidden;

  .works_start {
    margin-top: -300px;
    width: 100%;
    height: 100vh;
    z-index: -1000;
    overflow: hidden;
  }

  .work {
    height: 300vh;
    border-radius: 4vw;
    overflow: hidden;

    &.w1 {
      background-color: #35383f;
      // margin-top: 600vh;
      position: relative;
    }

    &.w2 {
      background-color: #5d6164;
      position: relative;
    }

    &.w3 {
      background-color: #8087b3;
      position: relative;
    }

    &.w4 {
      background-color: #293C70;
      position: relative;
    }

    .work_wrap {
      width: 100%;
      height: 100vh;
      position: relative;
      overflow: hidden;

      .top {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        color: var(--white);

        h2 {
          font-family: var(--gmark);
          font-weight: 600;
          font-size: 7vw;
          display: inline-block;
          line-height: 0.6;
          padding-top: 20px;
          margin-top: 2.5vw;
          letter-spacing: -0.3vw;
        }

        span {
          display: inline-block;
          font-family: var(--pre);
          font-size: 1vw;
          padding-left: 25px;
          line-height: 1;
        }
      }

      .desc {
        padding-top: 80px;
        // padding-left: 35px;
        text-align: center;
        color: var(--white);
        font-size: 16px;
        line-height: 1.2;
        font-family: var(--ilbo);
      }

      .img {
        position: absolute;
        width: 25%;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: -1;

        // z-index: -1;
        img {
          border-radius: 3vw;
        }
      }

      .works_nav {
        a {
          padding: 10px 40px;
          border-radius: 50px;
          font-family: var(--zeno);
          font-size: 4vw;
          color: var(--white);

          &:hover {
            color: var(--black);
          }

          &:first-child {
            position: absolute;
            bottom: 20px;
            left: 20px;
          }

          &:last-child {
            position: absolute;
            bottom: 20px;
            right: 20px;
          }
        }
      }
    }
  }






  @media (max-width: 1000px) {
    // .works_start {
    //   height: 80vh;
    // }

    .work {
      height: 200vh;

      .work_wrap {
        height: 100vh;
        // margin-top: -7vw;
        overflow: auto;
        // height: 70vh;


        .top h2 {
          font-weight: 600;
        }

        .desc {
          font-size: 14px;
          padding-left: 20px;
          padding-right: 20px;
        }

        .img {
          z-index: 0;
          border-radius: 0;
        }

        .img img {
          border-radius: 3vw;
        }

        .works_nav a:first-child {
          bottom: 75px;
          font-size: 24px;

        }

        .works_nav a:last-child {
          bottom: 75px;
          font-size: 24px;

        }
      }


    }

  }


}

@media (max-width: 650px) {
  #works {
    // height: calc(320vh - 400px);
  }

  .works_start {
    height: 60vh;
  }

  .work,
  .work_wrap {
    // height: 60vh;
  }

  .work .desc {
    font-size: 2vw;
  }

  .work .top span {
    display: none;
  }
}

@media (max-width: 479px) {
  #works {
    // height: 185vh;


    .work,
    .work_wrap {
      height: 100vh;
    }



    .work {
      border-radius: 0;

      &.w1 {
        border-radius: 4vw 4vw 0 0;
      }
      &.w4 {
        border-radius: 0 0 4vw 4vw;
      }

      .work_wrap {
        .desc {
          text-align: left;
          color: var(--black500);
          // padding: 5vw 35px;
          word-break: keep-all;
        }

        .works_nav a:first-child {
          bottom: 35px;
        }

        .works_nav a:last-child {
          bottom: 35px;
        }
      }
    }
  }

}

