#contact {
    margin-top: -14vh;

    .contact_wrap {
        width: 100%;
        background-color: var(--black);
        color: var(--white);
        border-radius: 5vw 5vw 0 0;
        position: relative;
        display: flex;
        flex-direction: column;

        font-family: var(--sui);


        .contact_text_top {
            padding: 24vh 5vw 0 5vw;

            .text {
                z-index: 1;
                font-family: var(--zeno);

                &.t1 {
                    font-size: 3vw;
                    font-family: var(--gmark);
                    font-weight: 900;
                    line-height: 1;
                }

                &.t2 {
                    line-height: 1;
                    font-size: 7.5vw;
                    padding-left: 5vw;
                }

                &.t3 {
                    font-size: 12vw;
                    line-height: 0.8;
                    padding-left: 8vw;
                    margin-top: -2vw;
                    position: relative;
                    z-index: 300;
                    white-space: nowrap;
                }
            }
        }

        .top {
            >h2 {
                display: flex;
                justify-content: space-between;
                font-size: 12vw;
                font-weight: 500;
                letter-spacing: -0.9vw;
                line-height: 9.1vw;
                margin-left: -1vw;
            }
        }

        .mid {
            margin: 0 5vw;
            display: flex;
            // width: 100%;
            height: 100%;
            flex-direction: column;
            position: relative;

            .img_wrap {
                max-width: 50vw;
                width: auto;
                height: 80vh;

                img {
                    width: auto;
                    height: 100%;
                }

                >h2 {
                    >p {
                        text-align: center;
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        font-size: 9vw;
                        color: var(--yellow);
                        line-height: 0.76;
                        // &:hover {
                        //     color: var(--white);
                        // }
                        // > span {
                        //     font-size: 14px;
                        //     position: absolute;
                        //     right: 2vw;
                        //     bottom: -2vw;
                        //     color: var(--black400);
                        //     cursor: auto;
                        // }
                    }
                }
            }

            >h2 {

                // width: 50%;
                >p {
                    font-size: 11vw;
                    display: inline-block;
                    font-weight: 500;
                    letter-spacing: -0.9vw;
                    line-height: 9.1vw;
                    height: 100%;
                }
            }

            @media (max-width: 1000px) {
                .img_wrap {
                    width: 100%;
                    max-width: 100%;
                    // width: 80vw;
                    height: auto;

                    img {
                        width: 100%;
                        // width: auto;
                        height: auto;
                    }
                }
            }

            @media (max-width: 479px) {
                .img_wrap {
                    width: 100%;
                    max-width: auto;
                    // width: 80vw;
                    // height: auto;

                    img {
                        width: 100%;
                        // width: auto;
                        // height: 100%;
                    }

                    // >h2 {
                    //     >p {
                    //         text-align: center;
                    //         position: absolute;
                    //         bottom: 0;
                    //         right: 0;
                    //         font-size: 9vw;
                    //         color: var(--yellow);
                    //         line-height: 0.76;
                    //         // &:hover {
                    //         //     color: var(--white);
                    //         // }
                    //         // > span {
                    //         //     font-size: 14px;
                    //         //     position: absolute;
                    //         //     right: 2vw;
                    //         //     bottom: -2vw;
                    //         //     color: var(--black400);
                    //         //     cursor: auto;
                    //         // }
                    //     }
                    // }
                }

                >h2 {

                    // width: 50%;
                    >p {
                        font-size: 11vw;
                        display: inline-block;
                        font-weight: 500;
                        letter-spacing: -0.9vw;
                        line-height: 9.1vw;
                        height: 100%;
                    }
                }
            }
        }

        .bot {
            padding: 0 5vw;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            .top_button {
                margin-top: 300px;
                margin-bottom: 170px;
                padding: 0 35px;
                border-radius: 30px;
                border: 1px solid var(--yellow);
                color: var(--yellow);
                display: inline-block;

                &:hover {
                    border: 1px solid var(--white);
                    color: var(--white);
                }
            }

            @media (max-width: 479px) {
                .top_button {
                    margin: 100px 0;
                    padding: 0 20px;
                }
            }

            .text {
                font-family: var(--pre);
                font-size: 1vw;
                font-weight: 400;
                line-height: 1vw;
            }

            .mail {
                font-family: var(--zeno);
                font-size: 5vw;
                font-weight: 300;
                letter-spacing: -0.2vw;
                margin-top: 0;
                padding-top: 0;
                padding-bottom: 0;
                line-height: 4.5vw;
                border-bottom: 1px solid #fff;

                &:hover {
                    color: var(--yellow);
                }
            }
        }

        .sns {
            padding: 30px 1vw 1vw;
            display: flex;
            justify-content: space-between;

            font-family: var(--sui);
            text-transform: uppercase;

            span {
                display: flex;
                flex-direction: column;
                font-size: 1vw;

                a {
                    font-size: 3vw;
                    font-weight: 600;
                    line-height: 3vw;
                    letter-spacing: -0.1vw;

                    &:hover {
                        color: var(--yellow);
                    }
                }

                &:nth-child(1) {
                    text-align: left;
                }

                &:nth-child(2) {
                    text-align: center;
                }

                &:nth-child(3) {
                    text-align: right;
                }
            }
        }
    }
}